const ImageRound = (props) => {
    return (
        <div>
            <div className="image">
                <img src={props.src} alt={props.alt} />
            </div>
            <style jsx>{`
                .image {
                    display: flex;
                    border-radius: 10%;
                    overflow: hidden;
                    border: 8px solid var(--mainColor);
                }
            `}</style>
        </div>
    )
}

export default ImageRound